<template>
  <div class="bankindex paperbank">
    <div class="paperbody main" id="paper">
      <div class="title">
        <span><i class="el-icon-d-arrow-right"></i>审核考试场次</span>
        <div class="fr"><el-button @click="$router.back(-1)" round type="primary" plain>返回</el-button></div>
      </div>
      <div class="body-content">
        <div class="test-list">
          <div class="list">
            <div class="list-title">
              <span>试卷基本信息</span>
              <i id="test-title"></i>
            </div>
            <div class="check-btn">
              <el-button-group>
                <el-button type="success" icon="el-icon-check" @click="subPass"></el-button>
                <el-button type="warning" icon="el-icon-close" @click="subNoPass"></el-button>
              </el-button-group>
            </div>
            <div class="list-subject">
              <ul class="subjectlist" id="listi">
                <li :key="index" v-for="(item, index) in questionTypeList" :class="{clickon: item.tag === selTag}" v-on:click="swichchapters(item.tag)">
                  {{ item.name }}
                </li>
              </ul>
            </div>
            <div class="list-notes">
              <div class="notes-title">
                <span>试卷考法</span>
                <span>按总时长计时</span>
              </div>
              <div class="notes-others">
                <div class="lit">创建者：
                  <span>{{bankInfo.createdbyid}}</span>
                </div>
                <div class="lit" v-if="bankInfo.uploadbyid">最近修改者：
                  <span>{{bankInfo.uploadbyid}}</span>
                </div>
                <div class="lit">试卷类型:
                  <span>{{bankInfo.examtype | examtypeTransform}}</span>
                </div>
                <div class="lit" v-if="bankInfo.sectionname">所属章：
                  <span>{{bankInfo.sectionname}}</span>
                </div>
                <div class="lit" v-if="bankInfo.sectionsname">所属节：
                  <span>{{bankInfo.sectionsname}}</span>
                </div>
                <div class="lit">试卷开始时间：
                  <span>{{bankInfo.timeopen}}</span>
                </div>
                <div class="lit">试卷结束时间：
                  <span>{{bankInfo.timeclose}}</span>
                </div>
                <div class="lit">试卷考试时间：
                  <span>{{bankInfo.timelimit}}</span>分钟
                </div>
                <div class="lit">选题方式：
                  <span>{{bankInfo.shufflequestions | shufflequestionsTranform}}</span>
                </div>
                <div class="lit">总题数：
                  <span>{{allBankList.length}}</span>题
                </div>
                <div class="lit">总分值：
                  <span>{{bankInfo.grade | gradeTransform}}</span>分
                </div>
                <div class="lit">评分方式：
                  <span>{{bankInfo.grademethod}}</span>分
                </div>
                <div class="lit">试卷说明：
                  <span>{{bankInfo.intro}}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="test-content">
          <div class="content-title">
            <span class="pre">预览试卷</span>
            <div class="oth">
              <span>试卷名称：{{bankInfo.name}}</span>
              <p v-if="bankInfo.check==6" class="check-status pass"><i class="el-icon-check"></i></p>
              <p v-else class="check-status nopass"><i class="el-icon-close"></i></p>
            </div>
          </div>
          <div v-if="selTag === 'danxuan'" class="content-body">
            <div class="body-tit">
              <span class="tit2"></span>
              <div class="other">
                <span>子卷：20分钟</span>
                <span class="others">10题</span>
                <span class="others">40分</span>
              </div>
            </div>
            <div class="body-test">
              <ul class="que">
                <li class="que-li" :key="index" v-for="(choice, index) in Multiplechoice">
                  <div class="tit">
                    <i>1.</i>
                    <span class="type">单选题</span>
                    <i>|</i>
                    <span class="cont">{{ choice.name }}</span>
                  </div>
                  <div class="bod">
                    <div class="a list">
                      <span>A.</span>
                      <span>{{ choice.choiceA }}</span>
                    </div>
                    <div class="b list">
                      <span>B.</span>
                      <span>{{ choice.choiceB }}</span>
                    </div>
                    <div class="c list">
                      <span>C.</span>
                      <span>{{ choice.choiceC }}</span>
                    </div>
                    <div class="d list">
                      <span>D.</span>
                      <span>{{ choice.choiceD }}</span>
                    </div>
                  </div>

                </li>
              </ul>
            </div>
          </div>
          <div v-if="selTag === 'wenda'" class="content-body" id="second-part">
            <div class="body-tit">
              <span class="tit1">第1子卷：</span>
              <span class="tit2">问答题（示例）</span>
              <div class="other">
                <span>子卷：20分钟</span>
                <span class="others">10题</span>
                <span class="others">40分</span>
              </div>
            </div>
            <div class="body-test">
              <ul class="que">
                <li class="que-li" :key="index" v-for="(part, index) in QuestionsandAnswers">
                  <div class="tit">
                    <i>1.</i>
                    <span class="type">问答题</span>
                    <i>|</i>
                    <span class="cont">{{ part.question }}</span>
                  </div>
                  <div class="bod">
                    <div class="a list">
                      <span>{{ part.answers }}</span>
                    </div>
                  </div>

                </li>
              </ul>
            </div>
          </div>
          <div v-if="selTag === 'tiankong'" class="content-body">
            <div class="body-tit">
              <span class="tit1">第1子卷：</span>
              <span class="tit2">填空题（示例）</span>
              <div class="other">
                <span>子卷：20分钟</span>
                <span class="others">10题</span>
                <span class="others">40分</span>
              </div>
            </div>
            <div class="body-test">
              <ul class="que">
                <li class="que-li" :key="index" v-for="(fill, index) in filltheblank">
                  <div class="tit">
                    <i>1.</i>
                    <span class="type">填空题</span>
                    <i>|</i>
                    <span class="cont">{{ fill.name }}</span>
                  </div>
                  <div class="bod">
                    <div class="a list">
                      <span>A.</span>
                      <span>{{ fill.choiceA }}</span>
                    </div>
                    <div class="b list">
                      <span>B.</span>
                      <span>{{ fill.choiceB }}</span>
                    </div>
                    <div class="c list">
                      <span>C.</span>
                      <span>{{ fill.choiceC }}</span>
                    </div>
                    <div class="d list">
                      <span>D.</span>
                      <span>{{ fill.choiceD }}</span>
                    </div>
                  </div>

                </li>
              </ul>
            </div>
          </div>
          <!--分页 start-->
          <div class="block">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="pagesizes" :page-size="pagesize" layout="total, sizes, prev, pager, next, jumper" :total="totals">
            </el-pagination>
          </div>
          <!--分页 end -->
        </div>
        <div class="icon-gotop" :class="{goTop: isTop}">
          <i class="iconfont" id="iconfont" v-on:click="goBacktop()">&#xe61b;</i>
          <span>回到顶部</span>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    let thisVue = this;

    return {
      bankInfo: {
        createdbyid: '',
        uploadbyid: '',
        grade: '',
        check: '',
        intro: '',
        name: '',
        examtype: '',
        timeopen: '',
        timeclose: '',
        timelimit: '',
        grademethod: '',
        sectionname: '',
        sectionsname: '',
        shufflequestions: ''
      },
      allBankList: [],
      // 当前选中的tag
      selTag: "danxuan",
      // 试卷列表数据
      questionTypeList: [
        {
          id: 12312,
          name: "选择题",
          tag: "danxuan"
        },
        {
          id: 34234,
          name: "问答题",
          tag: "wenda"
        },
        {
          id: 4454,
          name: "填空题",
          tag: "tiankong"
        }
      ],
      //选择题
      Multiplechoice: [
        {
          id: 11,
          name:
            "使用css使文字的居中的方法是有很多中的使文字水平剧中使用的text-aligin属性",
          choiceA: "内容",
          choiceB: "内容",
          choiceC: "内容",
          choiceD: "内容"
        },
        {
          id: 22,
          name: "使用css使文字的居中的方法是有很多",
          choiceA: "内容",
          choiceB: "内容",
          choiceC: "内容",
          choiceD: "内容"
        },
        {
          id: 22,
          name: "使用css使文字的居中的方法是有很多",
          choiceA: "内容",
          choiceB: "内容",
          choiceC: "内容",
          choiceD: "内容"
        },
        {
          id: 22,
          name: "使用css使文字的居中的方法是有很多",
          choiceA: "内容",
          choiceB: "内容",
          choiceC: "内容",
          choiceD: "内容"
        },
        {
          id: 22,
          name: "使用css使文字的居中的方法是有很多",
          choiceA: "内容",
          choiceB: "内容",
          choiceC: "内容",
          choiceD: "内容"
        },
        {
          id: 22,
          name: "使用css使文字的居中的方法是有很多",
          choiceA: "内容",
          choiceB: "内容",
          choiceC: "内容",
          choiceD: "内容"
        },
        {
          id: 22,
          name: "使用css使文字的居中的方法是有很多",
          choiceA: "内容",
          choiceB: "内容",
          choiceC: "内容",
          choiceD: "内容"
        },
        {
          id: 22,
          name: "使用css使文字的居中的方法是有很多",
          choiceA: "内容",
          choiceB: "内容",
          choiceC: "内容",
          choiceD: "内容"
        },
        {
          id: 22,
          name: "使用css使文字的居中的方法是有很多",
          choiceA: "内容",
          choiceB: "内容",
          choiceC: "内容",
          choiceD: "内容"
        }
      ],
      //问答题
      QuestionsandAnswers: [
        {
          id: 111,
          question: "这是一道问答题",
          answers: "这里是问答题的答案哦"
        },
        {
          id: 222,
          question: "这是另外的一道问答题哦",
          answers: "这里依旧是问答题的答案拉"
        }
      ],
      //填空题
      filltheblank: [
        {
          id: 1111,
          name: "这是一道填空题哦",
          choiceA: "内容",
          choiceB: "内容",
          choiceC: "内容",
          choiceD: "内容"
        },
        {
          id: 2222,
          name: "这是一道填空题哦",
          choiceA: "内容",
          choiceB: "内容",
          choiceC: "内容",
          choiceD: "内容"
        }
      ],
      //回到顶部
      isTop: false,
      //分页
      pagesizes: [5, 10, 20, 30, 40],
      totals: 40,
      pagesize: 10,
      currentPage: 4
    };
  },
  mounted() {
    var _this = this;
    //当滚动条的位置处于距顶部100像素以上时，跳转链接出现，否则消失
    $(window).scroll(function() {
      _this.isTop = ($(window).scrollTop() > 200) ? true : false;
    });
    this.requAllBank();
    this.requBankInfo();
  },
  methods: {
    //导航内容切换
    swichchapters: function(tag) {
      this.selTag = tag;
    },
    //回到顶部
    goBacktop: function() {
      if ($("html").scrollTop()) {
        $("html").animate({ scrollTop: 0 }, 1000);
        return false;
      }
      $("body").animate({ scrollTop: 0 }, 1000);
      return false;
    },
    //页码显示
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    // 通过审核
    subPass () {
      this.$confirm('审核通过的试卷不能在做任何修改, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$ajax.post(this.Const.apiUrl, {
          module: 'exam',
          controller: 'Quiz',
          action: 'audit',
          id: this.$route.query.d,
          status: '0',
          msg: ''
        }),then(response => {
          let resDate = response.data;
          if (resDate.status == 0) {
            this.$message({type: 'success', message: '审核成功！'});
          } else {
            this.$message({type: 'info', message: '审核失败！'});
          }
        }).catch(error => {
          this.$message({type: 'error', message: '审核失败！'});
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });          
      });
    },
    // 审核未通过
    subNoPass () {
      this.$prompt('请输入此试卷审核不通过原因', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /\S/,
        inputErrorMessage: '请输入此试卷审核不通过原因'
      }).then(({ value }) => {
        this.$ajax.post(this.Const.apiUrl, {
          module: 'exam',
          controller: 'Quiz',
          action: 'audit',
          id: this.$route.query.d,
          status: '1',
          msg: value
        }),then(response => {
          let resDate = response.data;
          if (resDate.status == 0) {
            this.$message({type: 'success', message: '审核成功！'});
          } else {
            this.$message({type: 'info', message: '审核失败！'});
          }
        }).catch(error => {
          this.$message({type: 'error', message: '审核失败！'});
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '取消输入'
        });       
      });
    },
    // 获取试卷的试题信息
    requAllBank () {
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      this.$ajax.post(this.Const.apiUrl, {
        module: 'exam',
        controller: 'Quiz',
        action: 'getQuizQuestionList',
        quiz: this.$route.query.d
      }).then(response => {
        loading.close();
        console.log('试题list' ,response);
        let resDate = response.data;
        if (resDate.status == 0) {
          this.allBankList = resDate.data;
        } else {
          this.allBankList = [];
          this.$message({type: 'info', message: resDate.data ? resDate.data : '获取信息失败！'});
        }
      }).catch(error => {
        loading.close();
        this.$message({type: 'error', message: '获取信息失败！'});
      });
    },
    // 获取试卷基础属性
    requBankInfo () {
      this.$ajax.post(this.Const.apiUrl, {
        module: 'exam',
        controller: 'Quiz',
        action: 'quizDetail',
        id: this.$route.query.d
      }).then(response => {
        // console.log('试题基础属性' ,response);
        let resDate = response.data;
        if (resDate.status == 0) {
          this.bankInfo = resDate.data;
        } else {
          this.$message({type: 'info', message: '获取试卷属性失败！'});
        }
      }).catch(error => {
        this.$message({type: 'error', message: '获取试卷属性失败！'});
      });
    }
  },
  filters: {
    shufflequestionsTranform (val) {
      return (val == 0) ? '自动选题' : '手动选题';
    },
    gradeTransform (val) {
      return parseInt(val);
    },
    examtypeTransform (val) {
      return (val == 0) ? '测试' : '考试';
    }
  }
};
</script>

<style lang="scss">
#paper {
  position: absolute;
  /*min-height: 400px;*/
  height: auto;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
  background-color: #f8f9fb;
  .test-list {
    position: relative;
    width: 200px;
    min-height: 500px;
    float: left;
    .list {
      .list-title {
        position: relative;
        width: 200px;
        height: 35px;
        margin: 0 auto;
        background-color: #28bdb9;
        color: white;
        text-align: center;
        font-size: 0.1rem;
        line-height: 35px;
        cursor: pointer;
      }
      .list-notes {
        position: relative;
        width: 200px;
        height: 205px;
        margin: 0 auto;
        color: black;
        text-align: center;
        border: 1px solid #f0f0f7;
        margin-top: 10px;
        background: white;
        .notes-title {
          position: relative;
          top: 10px;
          width: 180px;
          height: 65px;
          background: #fafafc;
          border: 1px solid #fafafc;
          margin: 0 auto;
          span:nth-child(1) {
            color: #baa8a0;
            font-size: 0.1rem;
            display: block;
            margin-top: 10px;
            text-align: left;
            margin-left: 10px;
          }
          span:nth-child(2) {
            color: black;
            font-size: 0.2rem;
            display: block;
            text-align: left;
            margin-left: 10px;
          }
        }
        .notes-others {
          position: relative;
          margin-top: 30px;
          text-align: left;
          .lit {
            position: relative;
            margin-left: 10px;
            margin-bottom: 8px;
            span {
              color: #f2842a;
              margin-right: 8px;
              font-weight: bold;
            }
          }
        }
      }
      .check-btn {
        width: 200px;
        margin: 10px auto 40px;
        .el-button {
          width: 100px;
          border-radius: 0;
          height: 50px;
        }
      }
    }
    .list-subject{
      margin-top: 10px;
      ul li {
        position: relative;
        width: 200px;
        @include middelH(42px);
        color: black;
        text-align: center;
        font-size: 0.1rem;
        border: 1px solid #f0f0f7;
        background: #fbfbfc;
        margin: 0 auto;
        box-sizing: border-box;
        cursor: pointer;
        box-shadow: 0px 1px 1px #dde0e7;
        .click {
          color: #28bdb9;
          border: 1px solid #28bdb9;
        }
        &.clickon,
        &:hover {
          color: #28bdb9;
          border-color: #28bdb9;
        }
      }
    } 
    
  }
  .test-content {
    position: relative;
    width: 920px;
    height: auto;
    margin-left: 40px;
    float: left;
    .content-title {
      position: relative;
      @include middelH(100px);
      background: white;
      box-shadow: 0px 1px 1px #dde0e7;
      .pre {
        color: gray;
        margin-left: 10px;
        float: left;
      }
      .oth {
        position: relative;
        text-align: center;
        color: black;
        overflow: hidden;
        .check-status{
          position: absolute;
          top: -4px;
          right: -38px;
          width: 110px;
          height: 50px;
          color: #fff;
          line-height: 60px;
          font-size: 20px;
          transform: rotate(45deg);
          text-align: center;
          &.pass{background: #28bdb9;}
          &.nopass{ background: #f56c6c;}
          i {
            transform: rotate(-45deg);
          }
        }
      }
    }
    .content-body {
      position: relative;
      width: 920px;
      background: white;
      border: 1px solid #eff1f6;
      border-top: #28bdb9;
      margin-top: 8px;
      .body-tit {
        position: relative;
        width: 770px;
        height: 45px;
        line-height: 45px;
        .tit1 {
          position: relative;
          margin-left: 16px;
        }
        .other {
          float: right;
          position: relative;
          margin-right: 15px;
          .others {
            margin-left: 10px;
          }
        }
      }
      .body-test .que .que-li {
        border: 1px solid #f0f0f7;
        margin: 10px;
        .tit {
          /*height: 40px;*/
          background: #fafafc;
          line-height: 40px;
          i {
            margin-left: 10px;
          }
          .type {
            color: #28bdb9;
          }
          .cont {
            /*margin-left: 10px;*/
            width: 630px;
            display: block;
            margin-left: 105px;
            margin-top: -41px;
          }
        }
        .bod .list {
          margin-left: 100px;
          margin-top: 8px;
          span:nth-child(2) {
            margin-left: 12px;
          }
          #test-title {
            position: absolute;
            right: -14px;
            top: 10px;
            /*设置高度和宽度全部都为0*/
            width: 0;
            height: 0;
            line-height: 0;
            /*设置或检索对象的边框宽度*/
            border-width: 7px;
            /*设置或检索对象的边框样式。 */
            border-style: solid;
            /*上边框透明，又边框透明，下边框显示，左边框透明*/
            border-color: transparent transparent transparent #28bdb9;
          }
        }
      }
    }
    .block {
      text-align: right;
      margin-top: 40px;
      margin-bottom: 20px;
    }
  } //回到顶部图标样式
  .icon-gotop {
    position: fixed;
    bottom: 120px;
    right: 55px;
    cursor: pointer;
    display: none;
    height: 40px;
    span {
      color: gray;
      display: block;
      margin-left: -10px;
    }
  }

  .iconfont {
    font-family: "iconfont" !important;
    font-size: 30px;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: gray;
  }

  .goTop {
    display: block;
  }
}

/* 回到顶部图标引入*/

@font-face {
  font-family: "iconfont";
  /* project id 666106 */
  src: url("https://at.alicdn.com/t/font_666106_t6a30nu9cjj4te29.eot");
  src: url("https://at.alicdn.com/t/font_666106_t6a30nu9cjj4te29.eot?#iefix")
      format("embedded-opentype"),
